import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { OrdersService } from '@jmart-cabinet/modules/orders/services/orders.service';
import { OrderCard } from '@jmart-cabinet/modules/orders/pages/card-order-page/order-card';
import { Observable, of } from 'rxjs';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';

@Component({
  selector: 'j-card-order-page',
  templateUrl: './card-order-page.component.html',
  styleUrls: ['./card-order-page.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class CardOrderPageComponent implements OnInit {
  public orderId: number;
  public order$: Observable<OrderCard>;
  readonly themesMain = [
    { height: '1rem', width: '70%', margin: '2%',  'background-color': '#ECEEF1' },
    { height: '1rem', width: '70%', margin: '2%', 'background-color': '#ECEEF1' },
    { height: '3rem', width: '80%', margin: '2%', 'background-color': '#ECEEF1' },
  ];
  readonly themesTab = [
    { height: '10rem', width: '56%', margin: '2%',  'background-color': '#ECEEF1' },
    { height: '10rem', width: '36%', margin: '2%', 'background-color': '#ECEEF1' },
    { height: '10rem', width: '56%', margin: '2%',  'background-color': '#ECEEF1' },
    { height: '10rem', width: '36%', margin: '2%', 'background-color': '#ECEEF1' },
  ];
  readonly accessStatus = {
    C: true,
    R: true,
    H: true,
    GP: true,
    GDJ: true,
    GD: true,
    M: true,
  };
  constructor(private router: Router,
              private ordersService: OrdersService,
              private activateRoute: ActivatedRoute,
              private analytics: AnalyticsManagerAbstract,) {
    this.orderId = +this.activateRoute.snapshot.params?.id;
  }

  ngOnInit(): void {
    this.getOrder();
  }
  getOrder() {
    this.order$ = this.ordersService.getOrder(this.orderId);
  }
  onClose() {
    const back = this.activateRoute.snapshot.queryParams.back;
    if (back){
      this.router.navigate(['jmart-cabinet', back]);
      return;
    }
    this.router.navigate(['jmart-cabinet', 'orders', 'main'], { queryParamsHandling: 'preserve' });
  }
  refreshOrder(event, order: OrderCard) {
    this.analytics.logEvent('JMartOrderCard_EditStatus', {
      version: 1,
      order_id: order.orderNumber,
      current_status: order.currentStatus.status,
      ...event,
      payment_method: order.orderInfo?.paymentMethod,
      delivery_type: order.orderInfo?.deliveryType,
      delivery_address: order.orderInfo?.deliveryAddress,
      buyers_city: order.orderInfo?.city,
      point_of_issue: order.orderInfo?.pointOfIssue,
      buyers_notes: Boolean(order.orderInfo?.city),
      jmart_notes: Boolean(order.comments?.jmart),
      me_notes: Boolean(order.comments?.merchant),
      sum: order.productsListTotal?.discountPrice,
      number_of_product: order.productsList?.length,
      number_of_positions: order.productsListTotal?.amount


    })
    this.order$ = of();
    this.getOrder();
  }

}
