import { Component, Input, OnInit } from '@angular/core';
import { OrderComments } from '@jmart-cabinet/modules/orders/pages/card-order-page/order-card';
import { debounceTime, distinctUntilChanged, map, switchMap, tap } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { OrdersService } from '@jmart-cabinet/modules/orders/services/orders.service';

@Component({
  selector: 'j-card-order-notes',
  templateUrl: './card-order-notes.component.html',
  styleUrls: ['./card-order-notes.component.scss']
})
export class CardOrderNotesComponent implements OnInit {
  @Input() comments: OrderComments;
  @Input() orderNumber: number;
  private searchStream = new Subject<string>();
  loading = false;
  constructor(private ordersService: OrdersService) { }

  ngOnInit(): void {
    this.searchStream.pipe(
      debounceTime(1000),
      distinctUntilChanged(),
      tap(() => this.loading = true),
      switchMap(searchTerm => {
        return this.ordersService.editComments(this.orderNumber, searchTerm);
      })
    ).subscribe(data => {
      this.loading = false;
    });
  }
  editComments(event) {
    this.comments.merchant = event;
    this.searchStream.next(event);
  }
}
