import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JmartCabinetNoLayoutComponent } from '@jmart-cabinet/components/jmart-cabinet-no-layout/jmart-cabinet-no-layout.component';
import { CardOrderPageComponent } from '@jmart-cabinet/modules/orders/pages/card-order-page/card-order-page.component';


const routes: Routes = [
  {
    path: '',
    component: JmartCabinetNoLayoutComponent,
    children: [
      {
        path: ':id',
        component: CardOrderPageComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CardOrderPageRoutingModule { }
