import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-card-order-alert',
  templateUrl: './card-order-alert.component.html',
  styleUrls: ['./card-order-alert.component.scss']
})
export class CardOrderAlertComponent implements OnInit {
  @Input() alertMessage;
  @Input()  type = 'message';
  loading = false;
  @Output() refresh = new EventEmitter();
  comments = '';
  constructor(public modal: NgbActiveModal) { }

  ngOnInit(): void {
  }
  buttonClick() {
    this.modal.close();
  }
  revoke(){
    this.loading = true;
    this.alertMessage.comment = this.comments;
    this.refresh.emit(this.alertMessage);
  }
}
