import { Directive, Input, OnInit } from '@angular/core';
import { OrderCard } from '@jmart-cabinet/modules/orders/pages/card-order-page/order-card';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import { ActivatedRoute, Router } from '@angular/router';

@Directive({
  selector: '[jCardOrderAnalytics]',

})
export class CardOrderAnalyticsDirective implements OnInit{
  @Input('jCardOrderAnalytics') orderCard: OrderCard;
  private source: 'orderslist' | 'orderslist_search' | 'refresh' | 'direct' = 'direct';
  constructor(private analytics: AnalyticsManagerAbstract,
              private router: Router,
              private route: ActivatedRoute) {
    this.navigationType()
    console.log(this.source)
  }
  ngOnInit() {
    this.analytics.logEvent('JMartOrderCard_Start', {
              version: 1,
              order_id: this.orderCard.orderNumber,
              source: this.source,
              payment_method: this.orderCard.orderInfo?.paymentMethod,
              delivery_type: this.orderCard.orderInfo?.deliveryType,
              delivery_address: this.orderCard.orderInfo?.deliveryAddress,
              buyers_city: this.orderCard.orderInfo?.city,
              point_of_issue: this.orderCard.orderInfo?.pointOfIssue,
              buyers_notes: Boolean(this.orderCard.orderInfo?.city),
              jmart_notes: Boolean(this.orderCard.comments?.jmart),
              me_notes: Boolean(this.orderCard.comments?.merchant),
              sum: this.orderCard.productsListTotal?.discountPrice,
              number_of_product: this.orderCard.productsList?.length,
              number_of_positions: this.orderCard.productsListTotal?.amount
    })
  }
  navigationType() {
    if (this.route.snapshot.queryParams?.source === 'orderslist') {
      this.route.snapshot.queryParams?.terms?.length ? this.source = 'orderslist_search' : this.source = 'orderslist'
      return this.router.navigate([], {queryParams: {source: null}, queryParamsHandling: 'merge'});
    }
    const source = (window.performance.getEntriesByType("navigation")[0] as PerformanceNavigationTiming).type;
    source === 'reload' ? this.source = 'refresh' : this.source = 'direct';
  }
}
